  display: inline-block;
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
}
.tglider-outer .tglider-item:before,
.tglider-outer .tglider-img-wrap:before {
  content: "";
  display: inline-block;
  height: 50%;
  width: 1px;
  margin-right: -1px;
}
.tglider-outer .tglider-img-wrap {
  position: static;
  padding: 0 5px;
}
.tglider-outer .tglider-item.tglider-complete {
  background-image: none;
}
.tglider-outer .tglider-item.tglider-current {
  z-index: 1060;
}
.tglider-outer .tglider-image {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  max-height: 100%;
  width: auto !important;
  height: auto !important;
}
.tglider-outer.tglider-show-after-load .tglider-item .tglider-object,
.tglider-outer.tglider-show-after-load .tglider-item .tglider-video-play {
  opacity: 0;
  -webkit-transition: opacity 0.15s ease 0s;
  -o-transition: opacity 0.15s ease 0s;
  transition: opacity 0.15s ease 0s;
}
.tglider-outer.tglider-show-after-load .tglider-item.tglider-complete .tglider-object,
.tglider-outer.tglider-show-after-load .tglider-item.tglider-complete .tglider-video-play {
  opacity: 1;
}
.tglider-outer .tglider-empty-html {
  display: none;
}
.tglider-outer.tglider-hide-download #tglider-download {
  display: none;
}

.tglider-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1040;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  -webkit-transition: opacity 0.15s ease 0s;
  -o-transition: opacity 0.15s ease 0s;
  transition: opacity 0.15s ease 0s;
}
.tglider-backdrop.in {
  opacity: 1;
}

.tglider-css3.tglider-no-trans .tglider-prev-slide,
.tglider-css3.tglider-no-trans .tglider-next-slide,
.tglider-css3.tglider-no-trans .tglider-current {
  -webkit-transition: none 0s ease 0s !important;
  -moz-transition: none 0s ease 0s !important;
  -o-transition: none 0s ease 0s !important;
  transition: none 0s ease 0s !important;
}
.tglider-css3 .no-trans.tglider-prev-slide, .tglider-css3 .no-trans.tglider-next-slide, .tglider-css3 .no-trans.tglider-current {
  -webkit-transition: none 0s ease 0s !important;
  -moz-transition: none 0s ease 0s !important;
  -o-transition: none 0s ease 0s !important;
  transition: none 0s ease 0s !important;
}
.tglider-css3.tglider-use-css3 .tglider-item {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
.tglider-css3.tglider-use-left .tglider-item {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
.tglider-css3.tglider-fade .tglider-item {
  opacity: 0;
}
.tglider-css3.tglider-fade .tglider-item.tglider-current {
  opacity: 1;
}
.tglider-css3.tglider-fade .tglider-item.tglider-prev-slide, .tglider-css3.tglider-fade .tglider-item.tglider-next-slide, .tglider-css3.tglider-fade .tglider-item.tglider-current {
  -webkit-transition: opacity 0.1s ease 0s;
  -moz-transition: opacity 0.1s ease 0s;
  -o-transition: opacity 0.1s ease 0s;
  transition: opacity 0.1s ease 0s;
}
.tglider-css3.tglider-slide.tglider-use-css3 .tglider-item {
  opacity: 0;
}
.tglider-css3.tglider-slide.tglider-use-css3 .tglider-item.tglider-prev-slide {
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
}
.tglider-css3.tglider-slide.tglider-use-css3 .tglider-item.tglider-next-slide {
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}
.tglider-css3.tglider-slide.tglider-use-css3 .tglider-item.tglider-current {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  opacity: 1;
}
.tglider-css3.tglider-slide.tglider-use-css3 .tglider-item.tglider-prev-slide, .tglider-css3.tglider-slide.tglider-use-css3 .tglider-item.tglider-next-slide, .tglider-css3.tglider-slide.tglider-use-css3 .tglider-item.tglider-current {
  -webkit-transition: -webkit-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
  -moz-transition: -moz-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
  -o-transition: -o-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
  transition: transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
}
.tglider-css3.tglider-slide.tglider-use-left .tglider-item {
  opacity: 0;
  position: absolute;
  left: 0;
}
.tglider-css3.tglider-slide.tglider-use-left .tglider-item.tglider-prev-slide {
  left: -100%;
}
.tglider-css3.tglider-slide.tglider-use-left .tglider-item.tglider-next-slide {
  left: 100%;
}
.tglider-css3.tglider-slide.tglider-use-left .tglider-item.tglider-current {
  left: 0;
  opacity: 1;
}
.tglider-css3.tglider-slide.tglider-use-left .tglider-item.tglider-prev-slide, .tglider-css3.tglider-slide.tglider-use-left .tglider-item.tglider-next-slide, .tglider-css3.tglider-slide.tglider-use-left .tglider-item.tglider-current {
  -webkit-transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
  -moz-transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
  -o-transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
  transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
}